.gameboard {
    display: flex;
    padding: 25px;
    margin: 25px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

.card {
    border: 2px solid rgba(160, 0, 0, 0.531);
    border-radius: 15px;
    box-shadow: 5px 5px 10px;
    margin: 25px;
    transition: 0.3s;

}

.card:hover {
    box-shadow: 10px 10px 20px red;
    transition: 0.4s;
}

.card-name {
    color: white;
    background-color: black;
}

img {
    max-width: 300px;
    max-height: 400px;
    border-radius: 15px;
}

hr {
    width: 90%;
    border-top: 2px solid rgb(112, 3, 3);
}