.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: max-content;
  min-height: 100vh;
  background: rgb(115, 0, 22);
  background: linear-gradient(0deg, rgba(115, 0, 22, 1) 0%, rgba(14, 0, 3, 1) 64%);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  /* background-color: black; */
  color: white;
}

